import React, { useState, useEffect } from 'react'
import Button from '../../components/Button/Button'
import './claim.less'
export default function Claim({lang,poolShow=true}) {
  const [remainingTime,setRemainingTime] = useState('')
  useEffect(() => {
		let interval = null;
			//计数器
			interval = window.setInterval(() => {
				let period = 3600 * 8;
				let current = parseInt(Date.now()/1000);
				let epochBegin = parseInt(current / period)*period;
				let dis = (epochBegin + period - current);
				let h = parseInt(dis / 3600);
				let m = parseInt((dis % 3600)/60)
				let s = parseInt(dis % 60) 
				setRemainingTime(`${h} ${lang['h']} ${m} ${lang['m']} ${s} ${lang['s']}`);
			},1000);
    return () => {
      interval && clearInterval(interval);
		};
		
  }, []);
  return (
    <div className='mining-claim'>
      <div className='mining-claim-info'>
        <div className='deri-num-slider'>
          <div className='deri-claim-time'>
            {remainingTime}
          </div>
          {poolShow && <div className='deri-claim-text'>
            You have 2,100 unclaimed $DERI across all staked pools. You’ve earned a total of 23,694 Deri token since Sep 12, 2021. Woohoo!
          </div>}
          <div className='deri-claim-slider'>
          </div>
        </div>
        {poolShow && <div className='cliam-button'>
          <Button label={lang['claim-all']} />
        </div>}
      </div>
    </div>
  )
}